<template>
    <div class="wrapper" ref="scroll">
        <div class="content">
            <slot></slot>
        </div>
    </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
    name: "scroll",
    props: {
        probeType: {
            type: Number,
            default() {
                return 1
            }
        },
        pullUpLoad: {
            type: Boolean,
            default() {
                return false
            }
        }
    },
    data() {
        return {
            scroll: null,
            saveY: 0
        }

    },
    mounted() {
        this.scroll = new BScroll(this.$refs.scroll, {
            probeType: this.probeType,
            pullUpLoad: true,
            click: true,
            observeDOM: true,
            observeImage: true
        })
        this.scroll.on('scroll', (position) => {
            //console.log(position)
            this.$emit('scroll', position)
        })
        this.scroll.on('pullingUp', () => {
            //console.log('已经滑到了底部')
            this.$emit('loadmore')
            /* setTimeout(() => {
                this.scroll.finishPullUp()
            }, 2000); */
        })
    },
    methods: {
        scrollTo(x, y, time = 300) {
            //console.log('调用了函数')
            this.scroll.scrollTo(x, y, time)

        },
        finished() {
            setTimeout(() => {
                this.scroll.finishPullUp()
            }, 2000);
        },
        getsaveY() {
            return this.scroll ? this.scroll.y : 0
        }
    },
    activated() {
        this.scroll.refresh()
        this.scroll.scrollTo(0, this.saveY)
        //console.log(this.saveY)
    },
    deactivated() {
        this.saveY = this.getsaveY()
    }


}
</script>
<style scoped>
.wrapper {
    position: absolute;
    width:100%;
    top: 44px;
    bottom: 49px;
    overflow: hidden;
    
    /*  height: calc(100% - 93px);   */
    /* margin-top: 44px; */
}
</style>


