<template>
    <div class="login">
        <div class="contain">
            <div class="left">
                <img :src=imgUrl alt="">
            </div>
            <div class="right">
                <div>用户名：<el-input placeholder="请输入用户名" v-model="username"></el-input>
                </div>
                <div>密码: <el-input placeholder="请输入密码" v-model="userpwd" show-password></el-input>
                </div>
                <div class="button">
                    <el-button type="primary" @click="login">登录</el-button>
                    <el-button type="primary" @click="regmember">注册</el-button>
                </div>
            </div>

            <div class="register" v-if="register">
                <Scroll class="regscroll">
                    <div>用户名：<el-input placeholder="请输入用户名" ></el-input>
                    </div>
                    <div>密码: <el-input placeholder="请输入密码"  show-password></el-input>
                    </div>
                    <div>性别：
                        <div></div>
                        <input placeholder="男"  type="checkbox" :checked="sex==1"  @click="sex=1">男
                        <input placeholder="女"  type="checkbox" :checked="sex==2" @click="sex=2">女
                    </div>
                    <div>年龄: <el-input placeholder="请输入年龄"></el-input>
                    </div>
                    <div>邮箱: <el-input placeholder="请输入邮箱"></el-input>
                    </div>
                    <div class="button">

                        <el-button type="primary" @click="commit">确定</el-button>
                    </div>
                </Scroll>

            </div>


        </div>
    </div>
</template>
<script>
import { gettoken } from '@/network/headpage.js'
import Scroll from 'components/common/scroll/Scroll'
export default {
    name: 'login',
    data() {
        return {
            sex:1,
            username: 'tony',
            userpwd: '1234',
            register: false
        }
    },
    computed:{
        imgUrl() {
            if(this.register){
                return "https://cdn.jsdelivr.net/gh/Tony-hurry/imageBad@main//study-images/linux01.png"
            }else{
                return "http://127.0.0.1:8089/resource/img/%E6%99%B4%E5%A4%A9.png"
            }
        }
    },
    components: {
        Scroll
    },
    methods: {
        login() {
            let data = {
                username: this.username,
                userpwd: this.userpwd
            }
            gettoken(data).then((res) => {
                if(res.data.state==200){
                    this.$toast.show('登录成功！')
                    localStorage.setItem('token', res.data.token)
                    this.$store.commit('addtoken')
                }else{
                    this.$toast.show(res.data.msg)
                }
               
                
                //this.$store.commit('addition',res.data.info)
            })
        },
        regmember() {
            this.register = true
        },
        commit() {
            this.register = false
        }
    }
}
</script>
<style scoped lang="less">
.login {
    width: 80%;
    height: 400px;
    margin: 30px auto;
    border: 2px solid black;
    border-radius: 4px;
    background-color: rgba(100, 100, 100, .4);

    .contain {
        display: flex;
        height: 100%;
        position: relative;

        .left {
            flex: 3;
            padding: 20px;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                border-radius: 4px;

            }
        }

        .right {
            padding: 5px;
            right: 2;
            flex: 2;
            display: flex;
            flex-direction: column;
            justify-content: center;

            div {
                margin-top: 10px;
                color: #fff;

            }

            .button {

                display: flex;

            }
        }

        .register {
            position: absolute;
            top: 0px;
            right: 0px;
            padding: 5px;
            width: 40%;
            height: 100%;
            overflow: hidden;
            right: 2;
            flex: 2;
            display: flex;
            flex-direction: column;
            justify-content: center;
            background-color: #fff;
            transition:all linear .5s;
            .regscroll{
                top:0px;
                left:0px;
                bottom: 0px;
                margin:20px 0px;
                padding:5px;
                div {
                margin-top: 10px;
                color: black;
                font-family: '宋体';

            }

            .button {

                display: flex;

            }
            }


        }
    }
}
</style>


